const { useEffect,useState,createRef,useCallback,cloneElement } = React
export function ClassGroupSelectorComp(props) {
    const placeId = props.placeId
    const selectedPlaceId = props.selectedPlaceId ?? ""
    const onChange = props.onChange ?? null
    const [groups,setGroups] = useState(null)
    const [value,setValue] = useState(selectedPlaceId)

    useEffect(() => {
        gather(`places/${placeId}/groups`,res=>setGroups(res),err=>{

        })
    }, []);

    if(groups == null) {
        return <select></select>
    }

    return <select value={value} onChange={e => {
        setValue(e.target.value)
        onChange?.(parseInt(e.target.value))
    }}>
        <option value="0" key={0}>--wybierz--</option>
        {groups.map(gr => {
            const {
                id,
                timestamp,
                season,
                name,
                classesStartTime,
                classesEndTime,
                classesDay,
                priceForOneClass,
                sort
            } = gr
            return <option key={id} value={id}>{name}</option>
        })}

    </select>
}//<option value="0" key={0}>wypisany</option>